.card {
  display: inline-block;
  border: 2px dimgray solid;
  border-radius: 10px;
  margin: 15px;
  width: 35%;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.card img {
  width: 100%;
}

.card:hover {
  -webkit-box-shadow: 2px 4px 10px 2px rgba(0, 0, 0, 0.4);
          box-shadow: 2px 4px 10px 2px rgba(0, 0, 0, 0.4);
}

.text {
  -ms-flex-positive: 2;
      flex-grow: 2;
  padding: 5px;
}
.text p {
  padding: inherit;
}
.text h2 {
  padding: inherit;
}



